.order-manager {
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
    padding: 1.5rem;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;

    h1 {
        margin: 0;
        font-size: 1.8rem;
        color: #2c3e50;
    }
}

.filters {
    display: flex;
    flex-direction: column;
    margin-bottom: 1.5rem;
    gap: 1rem;

    @media (min-width: 768px) {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
}

.status-filters {
    display: flex;
    gap: 0.5rem;
    flex-wrap: wrap;
}

.filter-btn {
    background-color: #ecf0f1;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.9rem;
    transition: all 0.2s;
    white-space: nowrap;

    &:hover {
        background-color: #d6dbdf;
    }

    &.active {
        background-color: #3498db;
        color: white;
    }
}

.search {
    input {
        padding: 0.5rem 1rem;
        border: 1px solid #d6dbdf;
        border-radius: 4px;
        width: 250px;
        font-size: 0.9rem;

        &:focus {
            outline: none;
            border-color: #3498db;
            box-shadow: 0 0 0 2px rgba(52, 152, 219, 0.2);
        }
    }
}

.loading {
    text-align: center;
    padding: 2rem;
    color: #7f8c8d;
}

.no-orders {
    text-align: center;
    padding: 3rem;
    color: #7f8c8d;
}

.reset-btn {
    background: none;
    border: none;
    color: #3498db;
    cursor: pointer;
    margin-left: 0.5rem;
    text-decoration: underline;
}

.orders-table-wrapper {
    overflow-x: auto;
}

.orders-table {
    width: 100%;
    border-collapse: collapse;

    th,
    td {
        padding: 0.75rem 1rem;
        text-align: left;
    }

    th {
        background-color: #f8f9fa;
        font-weight: 500;
        color: #7f8c8d;
    }

    tr {
        border-bottom: 1px solid #ecf0f1;

        &:last-child {
            border-bottom: none;
        }
    }

    tbody tr:hover {
        background-color: #f8f9fa;
    }
}

.customer-info {
    .customer-name {
        font-weight: 500;
    }

    .customer-contact {
        font-size: 0.85rem;
        color: #7f8c8d;
    }
}

.order-id {
    font-family: monospace;
    font-weight: 600;
    color: #34495e;
}

.order-total {
    font-weight: 500;
    color: #2c3e50;
}

.status {
    display: inline-block;
    padding: 0.3rem 0.6rem;
    border-radius: 20px;
    font-size: 0.8rem;
    font-weight: 500;
    text-transform: capitalize;

    &.pending {
        background-color: rgba(241, 196, 15, 0.15);
        color: #f39c12;
    }

    &.pending_payment {
        background-color: rgba(230, 126, 34, 0.15);
        color: #e67e22;
    }

    &.delivering {
        background-color: rgba(155, 89, 182, 0.15);
        color: #8e44ad;
    }

    &.completed {
        background-color: rgba(46, 204, 113, 0.15);
        color: #27ae60;
    }

    &.cancelled {
        background-color: rgba(231, 76, 60, 0.15);
        color: #c0392b;
    }
}

.actions {
    display: flex;
    gap: 0.5rem;
    flex-wrap: wrap;

    button {
        padding: 0.3rem 0.6rem;
        border: none;
        border-radius: 4px;
        font-size: 0.8rem;
        cursor: pointer;
        font-weight: 500;
        transition: all 0.2s;

        &:hover {
            transform: translateY(-1px);
        }

        &:active {
            transform: translateY(0);
        }

        &:disabled {
            opacity: 0.6;
            cursor: not-allowed;
            transform: none;
        }
    }

    .view-btn {
        background-color: #3498db;
        color: white;

        &:hover {
            background-color: #2980b9;
        }
    }

    .process-btn {
        background-color: #2ecc71;
        color: white;

        &:hover {
            background-color: #27ae60;
        }
    }

    .ship-btn {
        background-color: #9b59b6;
        color: white;

        &:hover {
            background-color: #8e44ad;
        }
    }

    .deliver-btn {
        background-color: #27ae60;
        color: white;

        &:hover {
            background-color: #219653;
        }
    }

    .cancel-btn {
        background-color: #e74c3c;
        color: white;

        &:hover {
            background-color: #c0392b;
        }
    }
}

/* Modal Styles */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modal {
    background: white;
    border-radius: 8px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
    width: 90%;
    max-width: 500px;
    max-height: 90vh;
    overflow-y: auto;
}

.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem;
    border-bottom: 1px solid #ecf0f1;

    h3 {
        margin: 0;
        color: #2c3e50;
        font-size: 1.25rem;
    }

    .close-btn {
        background: none;
        border: none;
        font-size: 1.5rem;
        cursor: pointer;
        color: #7f8c8d;
        padding: 0;
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        transition: all 0.2s;

        &:hover {
            background-color: #ecf0f1;
            color: #2c3e50;
        }
    }
}

.modal-body {
    padding: 1.5rem;

    p {
        margin-bottom: 1rem;
        color: #2c3e50;
        line-height: 1.5;
    }

    .form-group {
        margin-bottom: 1rem;

        label {
            display: block;
            margin-bottom: 0.5rem;
            font-weight: 500;
            color: #2c3e50;
        }

        textarea {
            width: 100%;
            padding: 0.75rem;
            border: 1px solid #d6dbdf;
            border-radius: 4px;
            font-size: 0.9rem;
            font-family: inherit;
            resize: vertical;
            min-height: 80px;

            &:focus {
                outline: none;
                border-color: #3498db;
                box-shadow: 0 0 0 2px rgba(52, 152, 219, 0.2);
            }
        }
    }
}

.modal-footer {
    display: flex;
    justify-content: flex-end;
    gap: 0.75rem;
    padding: 1.5rem;
    border-top: 1px solid #ecf0f1;

    button {
        padding: 0.75rem 1.5rem;
        border: none;
        border-radius: 4px;
        font-size: 0.9rem;
        font-weight: 500;
        cursor: pointer;
        transition: all 0.2s;

        &:disabled {
            opacity: 0.6;
            cursor: not-allowed;
        }
    }

    .cancel-modal-btn {
        background-color: #ecf0f1;
        color: #2c3e50;

        &:hover:not(:disabled) {
            background-color: #d6dbdf;
        }
    }

    .confirm-reject-btn {
        background-color: #e74c3c;
        color: white;

        &:hover:not(:disabled) {
            background-color: #c0392b;
        }
    }
}
